@use '../base/variables' as v;
@use '../base/mixins' as m;

.formulario {
    margin-top: 5rem;
    width: 90%;
}

.campo {
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label {
        flex: 0 0 10rem;
    }
    input {
        flex: 1; //esto hace que tome el resto del tamaño
        border: none;
        padding: 1rem;
        border-radius: 1rem;
        background-color: lighten($color: v.$gris, $amount: 40%);

        &:disabled {
            background-color: lighten(v.$negro, 30%);
            color: v.$blanco;
            cursor: not-allowed;
        }
    }
}