@use '../base/mixins' as m;
@use '../base/variables' as v;

.no-proyectos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
        font-size: 5rem;
    }
    a {
        color: v.$indigo;
    }
}

.listado-proyectos {
    list-style: none;
    margin-top: 3rem;
    padding: 0;
    @include m.grid(1, 2rem);
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
    @include m.desktop {
        @include m.grid(3, 2rem);
    }

    .proyecto {
        background-color: lighten($color: v.$indigo, $amount: 5%);
        min-height: 10rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: 1rem;

        &:hover {
            background-color: darken($color: v.$indigo, $amount: 5%);
        }
        
        a {
            color: v.$blanco;
        }
    }
}