@use '../base/mixins' as m;
@use '../base/variables' as v;

.filtros {
    background-color: lighten($color: v.$blanco, $amount: 20%);
    margin-top: 2rem;
    border-radius: 1rem;
    padding: 2rem;
    
    .filtros-inputs {
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 2rem;
        }
    }
    
    input[type="radio"] {
        accent-color: v.$indigo;
        width: 1.6rem;
        height: 1.6rem;
    }

    h2 {
        color: v.$indigo;
        font-size: 2.8rem;
        @include m.tablet {
            margin: 0;
        }
    }

    .campo {
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
        margin-right: 2rem;
        @include m.tablet {
            margin-bottom: 0;
        }
    }

}